.header {
  width: 100%;
  height: 60px;
  padding: 10px 20px;
  box-sizing: border-box;
  position: sticky;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  background-color: #2f2c2f;
  color: #fff;
  z-index: 100;
}

.header__container {
  max-width: 1200px;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  width: 40px;
  height: 40px;
  margin: 0;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .header {
    padding: 10px;
  }
}